import React from "react";
import {Result,Tag, Typography, Button, Skeleton, Icon} from "antd";
import {withRouter} from "react-router-dom";

const { Paragraph, Text } = Typography;
@withRouter
class DetectResultCard extends React.Component {

    getResult(state) {
        if (state === '') {
            return (<Result
                status="success"
                title=""
                subTitle="Congratulationsthe video you have upload has been verified successfully."
            />)
        } else if (state === '') {
            return (<Result
                status="error"
                title=""
                subTitle="Sorrythe video you have upload failed to pass the verification."
                extra={[
                    <Button style={{width:200}} onClick={()=>{this.props.history.push('/home/appeal')}} key="console">
                        
                    </Button>,
                ]}
            >
                <div className="desc">
                    <Paragraph>
                        <Text
                            strong
                            style={{
                                fontSize: 16,
                            }}
                        >
                            :
                        </Text>
                    </Paragraph>
                    <Paragraph>
                        <Icon style={{ color: 'red' }} type="close-circle" />  <Tag>{this.props.copy.id}</Tag>
                    </Paragraph>
                </div>
            </Result>)
        } else if (state === '') {
            return (<Result
                icon={<Icon type="smile" theme="twoTone" />}
                title="!"
                subTitle="Please wait!Your video is under video detection."
            />)
        }
    }

    render() {
        return (
            <div className="result-card" style={
                {
                    background: "white",
                    borderRadius: "15px",
                    boxShadow: "0px 0px 20px rgba(137,137,137, 0.1)",
                    marginBottom: "20px",
                    padding: "20px"
                }}>
                <div style={{fontWeight: "bold", marginBottom: "20px", fontSize: "18px"}}></div>
                <Skeleton loading={this.props.loading} active>
                    {this.getResult(this.props.state)}
                </Skeleton>
            </div>
        )
    }
}

export default DetectResultCard


